import React from 'react';

const DiffPublishDates = (props) => {

        const arrayString = props.date;
        const toNum = arrayString.split(', ');
        const numDatesArray = toNum.map( item => +item );

                                // –≥–æ–¥,  –º–µ—Å—è—Ü, –¥–µ–Ω—å, —á–∞—Å, –º–∏–Ω—É—Ç–∞
                                // 2018,  0,  13,  15,  0
        const pubDate = new Date(numDatesArray[0], numDatesArray[1], numDatesArray[2], numDatesArray[3], numDatesArray[4]);
        const dateNow = new Date();

        const diffDays = Math.round( dateNow.getDate() - pubDate.getDate() );
        const diffHours = Math.round( dateNow.getHours() - pubDate.getHours() );
        const diffMinutes = Math.round( dateNow.getMinutes() - pubDate.getMinutes() );
        const diffMonths = Math.round( dateNow.getMonth() - pubDate.getMonth() );
        const diffYears = Math.round( dateNow.getFullYear() - pubDate.getFullYear() );

        const result =
            // –≥–æ–¥—ã
            ( diffYears === 1 || diffYears === 21) ? diffYears + ' –≥–æ–¥ –Ω–∞–∑–∞–¥' : // 1, 21
            ( (diffYears > 21 && diffYears < 25) || (diffYears > 1 && diffYears < 5) ) ? diffYears + ' –≥–æ–¥–∞ –Ω–∞–∑–∞–¥' : // 2-4, 22-24
            ( diffYears > 4 && diffYears < 21 ) ? diffYears + ' –ª–µ—Ç –Ω–∞–∑–∞–¥' : // 5-20,
            // –º–µ—Å—è—Ü—ã - 12
            ( diffMonths === 1 ) ? diffMonths + ' –º–µ—Å—è—Ü –Ω–∞–∑–∞–¥' : // 1
            ( diffMonths > 1 && diffMonths < 5 ) ? diffMonths + ' –º–µ—Å—è—Ü–∞ –Ω–∞–∑–∞–¥' : // 2-4
            ( diffMonths > 4 && diffMonths < 12 ) ? diffMonths + ' –º–µ—Å—è—Ü–µ–≤ –Ω–∞–∑–∞–¥' : // 5-12,
            // –¥–Ω–∏ - 31
            ( diffDays === 1 || diffDays === 21 ) ? diffDays + ' –¥–µ–Ω—å –Ω–∞–∑–∞–¥' : // 1, 21
            ( (diffDays > 21 && diffDays < 25) || (diffDays > 1 && diffDays < 5) ) ? diffDays + ' –¥–Ω—è –Ω–∞–∑–∞–¥' : // 2-4, 22-24
            ( (diffDays > 24 && diffDays <= 31) || (diffDays > 4 && diffDays < 21) ) ? diffDays + ' –¥–Ω–µ–π –Ω–∞–∑–∞–¥' : // 5-20, 25-30
            // —á–∞—Å—ã - 24
            ( diffHours === 1 || diffHours === 21 ) ? diffHours + ' —á–∞—Å –Ω–∞–∑–∞–¥' : // 1, 21
            ( ((diffHours > 21 && diffHours < 24) || (diffHours > 1 && diffHours < 5)) ) ? diffHours + ' —á–∞—Å–∞ –Ω–∞–∑–∞–¥' : // 2-4, 22-23
            ( diffHours > 4 && diffHours < 21 ) ? diffHours + ' —á–∞—Å–æ–≤ –Ω–∞–∑–∞–¥' : // 5-20
            // –º–∏–Ω—É—Ç—ã - 60
            (diffMinutes === 51 || diffMinutes === 41 || diffMinutes === 31 || diffMinutes === 21 || diffMinutes === 1) ? diffMinutes + ' –º–∏–Ω—É—Ç–∞ –Ω–∞–∑–∞–¥' :
            ( (diffMinutes > 51 && diffMinutes < 55) || (diffMinutes > 41 && diffMinutes < 45) || (diffMinutes > 31 && diffMinutes < 35) || (diffMinutes > 21 && diffMinutes < 25) || (diffMinutes <= 4 && diffMinutes > 1) ) ? diffMinutes + ' –º–∏–Ω—É—Ç—ã –Ω–∞–∑–∞–¥' :
            (diffMinutes >= 5 && diffMinutes <= 59) ? diffMinutes + ' –º–∏–Ω—É—Ç –Ω–∞–∑–∞–¥' :
            null;

        return (
            <div>
                <span>{result}</span>
            </div>
        )
};

export default DiffPublishDates;